export class RibbonBase extends BaseElement implements ITooltipPointer, IInfoConsumer {

	reg: IReg<IItemUiEnv>;

	get shortDescCtx(): IShortDescCtx {return DOMSH.findHost<SrcMain>(this)}

	get ribbonMode(): 'static' | 'hover' | 'button' {return (this.getAttribute('role') as 'static' | 'hover' | 'button' | null) || 'static'}

	set ribbonMode(mode: 'static' | 'hover' | 'button') {
		if (this.ribbonMode === mode) return;
		DOM.setAttr(this, 'mode', mode);
		if (mode === 'static') {
			DOM.setAttr(this, 'role', null);
			this.tabIndex = -1;
			this.onkeypress = null;
			//if(this.matches(':focus')) this.blur(); utile ?
		} else {
			DOM.setAttr(this, 'role', mode);
			this.tabIndex = 0;
			this.onkeypress = this.onKeyPress;
		}
	}

	/** Associe un tooltip Ã  ce ribbon. */
	protected setTooltip(factory: (owner: HTMLElement, tooltip: PopupTooltip) => IView | null, findFocusableOnShown?: boolean) {
		if (!factory) return;

		this.ribbonMode = 'hover';

		this.onclick = POPUP.promiseTooltip(this, findFocusableOnShown ?
				(owner: HTMLElement, tooltip: PopupTooltip): IView | null => {
					tooltip.addEventListener('c-shown', () => {
						const focusbale = DOMSH.findFlatNext(tooltip.firstElementChild, tooltip, DOM.IS_focusable);
						if (focusbale) focusbale.focus();
					});
					return factory(owner, tooltip);
				}
				: factory,
			{hoverAllowed: true, skin: "c-popup-floating", skinOver: "ribbon/tooltip"});
		this.onblur = (ev: FocusEvent) => {
			if (this.popupTooltip && ev.relatedTarget && !DOM.isAncestor(this.popupTooltip, ev.relatedTarget as HTMLElement)) this.popupTooltip.close();
		}
	}

	popupTooltip?: PopupTooltip;

	/** Associe une floating window Ã  ce ribbon. */
	protected setFloating(factory: (ribbon: RibbonBase, popup: PopupFloating) => IView | null, findFocusableOnShown?: boolean) {
		if (!factory) return;
		this.ribbonMode = 'button';
		this.onclick = () => {
			const popup = POPUP.showFloating((popup: PopupFloating) => {
				if (findFocusableOnShown) {
					popup.addEventListener('c-shown', () => {
						const focusbale = DOMSH.findFlatNext(popup.firstElementChild, popup, DOM.IS_focusable);
						if (focusbale) focusbale.focus();
					});
				}
				this.popupFloating = popup;
				popup.addEventListener('c-close', () => {
					this.popupFloating = null;
				});
				return factory(this, popup);
			}, this, this, {closeOnBlur: true, skin: "c-popup-floating", skinOver: "ribbon/floating scroll/small"});
		}
	}

	popupFloating?: PopupFloating;


	/**
	 * Si pas de tooltip via setTooltip() ni de setFloating(), mais affichage d'un menu par ex, appelle this.click() aprÃ¨s
	 * un temps de stabilisation de la souris au dessus du ribbon.
	 */
	protected callClickOnMouseover() {
		this.ribbonMode = 'button';
		POPUP.mouseOverRetarderCaller(this, false, () => this.click());
	}

	protected _initialize(init: any): void {
		this.reg = this.findReg(init);
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this.reg.installSkin("scroll/small", sr);
		this.reg.installSkin("wsp-rib", sr);
		this._initAndInstallSkin(this.localName, init);
		this.reg.env.infoBroker?.addConsumer(this);
	}

	/** Ribbon clickable. */
	onKeyPress(ev: KeyboardEvent) {
		if (LANG.in(ev.key, "Enter", " ") && !ACTION.isAnyControlPressed(ev)) {
			ev.stopImmediatePropagation();
			ev.preventDefault();
			this.click();
		}
	}

	protected _refresh() {
		super._refresh();
		if (this.popupTooltip) this.popupTooltip.close();
	}

	onViewHidden(closed?: boolean) {
		if (closed) this.reg?.env.infoBroker?.removeConsumer(this);
		if (this.popupTooltip) this.popupTooltip.close();
		if (this.popupFloating) this.popupFloating.close();
	}

	onInfo(info: IInfo): void {
		if (this.initialized) {
			if (info instanceof InfoRefreshItemDisplays) {
				const srcRef = info.srcRef;
				const currentSrcRef = SRC.srcRef(this.reg.env.longDesc);
				if (srcRef == currentSrcRef) this.refresh();
			}
		}
	}
}

