export class RibbonItemInfo extends RibbonBase {

	template: (rib: RibbonItemInfo) => TemplateResult;
	templateTt: (rib: RibbonItemInfo) => TemplateResult;

	protected _initialize(init: any): void {
		super._initialize(init);
		this.setAttribute("c-resizable", "");

		this.template = this.reg.getSvc("ribbon.item.info.tpl") || RibbonItemInfo._template;
		this.templateTt = this.reg.getSvc("ribbon.item.info.tplTt") || RibbonItemInfo._templateTt;

		this.reg.env.longDescChange.add(this.onLongDescChange.bind(this));
		this.setTooltip(this._tooltip);
		this.setAttribute("draggable", "");
		this.ondragstart = (ev: DragEvent) => ITEM.setShortDescTransferToDragSession({
			shortDescs: [this.reg.env.longDesc],
			emitter: this,
			reg: this.reg
		}, ev, 'native');
		this.ondragend = ITEM.resetShortDescTransferToDragSession;
	}

	_tooltip(this: void, owner: RibbonItemInfo, tooltip: PopupTooltip) {
		const root = <div id="ttRoot"/> as HTMLElement;
		owner.reg.installSkin(owner.localName, root.attachShadow(DOMSH.SHADOWDOM_INIT));
		tooltip.addEventListener('c-show', (ev: CustomEvent) => {
			renderAppend(owner.templateTt(owner), root.shadowRoot);
		});
		return root;
	}

	static _template(this: void, rib: RibbonItemInfo) {
		const longDesc = rib.reg.env.longDesc;
		const type = rib.reg.env.wsp.wspMetaUi.getItemType(longDesc.itModel);
		if (ITEM.isExtItem(longDesc.srcUri)) {
			return xhtml`<div>
<div id="itTi">${longDesc.itTi}</div>
<img align="center" src="${type.getIcon(longDesc)}"/><span id="srcCd">${SRC.extractLeafFromUri(longDesc.srcUri)}</span>
<span id="foreignItem"> (item Ã©tranger)</span>
<span id="itModel">${type.getTitle(longDesc)}</span></div>`;
		} else {
			return xhtml`<div>
<div id="itTi">${longDesc.itTi}</div>
<img align="center" src="${type.getIcon(longDesc)}"/><span id="srcCd">${SRC.extractLeafFromUri(longDesc.srcUri)}</span>
<span id="itModel">${type.getTitle(longDesc)}</span></div>`;
		}
	}

	static _templateTt(this: void, rib: RibbonItemInfo) {
		const longDesc = rib.reg.env.longDesc;
		const dt = new Date(longDesc.srcDt);
		const foreignActions = !ITEM.isExtItem(longDesc.srcUri) ? undefined :
			xhtml`<div id="actions">
				<c-button class="inline" i.="${{reg: rib.reg, icon: "/@skin@/wsp/actions/openItem.svg", title: "Ouvrir cet item Ã©tranger dans son atelier"} as OButtonInit}" e.click="${function (this: Button) {
				rib.reg.getSvc<IOpenForeignItemSvc>("openForeignItem")(rib.reg, WSP.extractWspCdFromWspRef(longDesc.itFullUriInOwnerWsp), WSP.extractSrcRefFromWspRef(longDesc.itFullUriInOwnerWsp));
			}}"/>
			</div>`;
		return xhtml`${foreignActions}<div id="srcDt">modifiÃ© le <span class="val">${dt.toLocaleDateString("fr")} Ã  ${dt.toLocaleTimeString("fr")}</span></div>
<div id="srcUser" b.hidden="${!longDesc.srcUser}">par <c-userref class="inline" i.="${{reg: rib.reg, nickOrAccount: longDesc.srcUser} as OUserRefInit}"/></div>
${rib.reg.getPref("wsp.hideWspStruct") ? undefined : xhtml`<div id="path">${ITEM.getItemUriLabel(longDesc.srcUri, longDesc)}</div>`}`;
	}

	protected _refresh() {
		super._refresh();
		renderAppend(this.template(this), this.shadowRoot);
	}

	onLongDescChange(ldNew: JSrcFields, ldOld: JSrcFields) {
		this.refresh();
	}
}

