/**
 *
 */
export class RibbonTabs extends RibbonBase {

	itemTabs: Tabs<SrcMain>

	protected _initialize(init: any) {
		super._initialize(init);
		this.reg.env.longDescChange.add(this.onLongDescChange.bind(this));
		this.itemTabs = DOMSH.findHost<SrcMain>(this)?.tabs;
		if (this.itemTabs) {
			const parent = this.itemTabs.shadowRoot.getElementById("tabs");
			const ctn = this.shadowRoot.appendChild(<div id="ctn"/>);
			//console.log("parentTabss:::",parent);
			ctn.style.minWidth = this.computeWidth(parent);
			new IntersectionObserver(cb => {
					//console.log("cb[0].intersectionRatio:::", cb[0].intersectionRatio);
					if (cb[0].intersectionRatio > .9) {
						//On peut insÃ©rer les onglets
						while (parent.firstElementChild) ctn.appendChild(parent.firstElementChild);
						DOM.setHidden(parent.parentElement, true);
					} else {
						//Place insuffisante, on replace dans l'emplacement d'origine
						while (ctn.firstElementChild) parent.appendChild(ctn.firstElementChild);
						DOM.setHidden(parent.parentElement, false);
					}
				},
				// [.8, 1] et margin V 10px pour Ã©viter erreur d'arrondis si zoom du nav et instabilitÃ© ou bloquage.
				{root: this, threshold: [.8, 1], rootMargin: "10px 0px"}).observe(ctn);
		}
	}

	protected computeWidth(parent: HTMLElement) {
		let min: number, max: number;
		if (getComputedStyle(this).direction === "rtl") {
			min = (parent.lastElementChild as HTMLElement).offsetLeft;
			max = (parent.firstElementChild as HTMLElement).offsetLeft + parent.firstElementChild.clientWidth;
		} else {
			min = (parent.firstElementChild as HTMLElement).offsetLeft;
			max = (parent.lastElementChild as HTMLElement).offsetLeft + parent.lastElementChild.clientWidth;
		}
		return ((max - min) * 1.1) + "px";
	}

	protected onLongDescChange() {
		const ctn = this.shadowRoot.getElementById("ctn");
		if(!ctn) return;
		if (ctn.firstElementChild) {
			//Les onglets sont ici, dans le ribbon, on les refresh (visibilitÃ© perms)
			for (let tab = ctn.firstElementChild as Tab; tab; tab = tab.nextElementSibling as Tab) {
				tab.refresh();
			}
			ctn.style.minWidth = this.computeWidth(ctn);
		} else {
			//Les onglets sont Ã  leur place initiale.
			ctn.style.minWidth = this.computeWidth(this.itemTabs.shadowRoot.getElementById("tabs"));
		}
	}
}

