/** Configuration de liste de liens */
export interface JRibbonRefsConf {

	label: string

	description?: string

	entriesMsg?: (nb: number | undefined) => string

	emptyGridMsg?: string

	shortViewRefs?: (this: HTMLElement, ctx: IRegPointer<IItemUiEnv>, result: JSrcFields[]) => TemplateResult

	fetchSrcList: (this: HTMLElement, ctx: IRegPointer<IItemUiEnv>) => Promise<JSrcFields[]>

	isVisible?: (this: HTMLElement, ctx: IRegPointer<IItemUiEnv>) => boolean

}

export interface ORibbonRefsInit extends OSkinableInit {
	/** Code de la liste fournissant les refs Ã  exploiter dans ce ribon. valeur par dÃ©faut : ribbon.item.refs.details
	 *		La **premiÃ¨re** entrÃ©e visible de la liste est celle qui alimente le total (forme repliÃ©e))
	 */
	refsListCode: string
}

export class RibbonRefs extends RibbonBase {

	confs: JRibbonRefsConf[];

	private conf: ORibbonRefsInit;

	private _noFetch = true;

	private _currentDetailsConf: JRibbonRefsConf;

	static DEFAULT_REFS_LIST = 'ribbon.item.refs.confs';

	private _total: number;

	protected _initialize(init: ORibbonRefsInit): void {
		this.conf = Object.assign({
			refsListCode: RibbonRefs.DEFAULT_REFS_LIST,
		}, init);
		super._initialize(this.conf);

		if (ITEM.isHistoryOrTrashUri(this.reg.env.longDesc.srcUri)) {
			this.hidden = true;
			return;
		}

		this.confs = this.reg.mergeLists(this.conf.refsListCode);
		this.confs = this.confs?.filter(entry => !entry.isVisible || entry.isVisible.call(this, this));
		this.setFloating(this.reg.getSvc("ribbon.item.refs.details") || this._details);

		// Load retardÃ© pour donner la prioritÃ© au contenu
		setTimeout(async () => {
			this._noFetch = false;
			this.refresh();
		}, 500);
	}

	async fetch(this: void, rib: RibbonRefs): Promise<JSrcFields[]> {
		return await rib.confs[0].fetchSrcList.call(rib, rib);
	}

	private redrawShortView(result?: JSrcFields[]): void {
		const total = result ? result.length : undefined;
		const defaultConf = this.confs[0];
		const tpl = defaultConf.shortViewRefs
			? defaultConf.shortViewRefs.call(this, this, result)
			: xhtml`<div id="total" title="${(defaultConf.entriesMsg ? defaultConf.entriesMsg(total) : null) || defaultConf.description || defaultConf.label}">${total !== undefined ? total : '...'}</div><div id="label">rÃ©f.</div>`;
		renderAppend(tpl, this.shadowRoot);
	}

	_details(this: void, rib: RibbonRefs, popup: PopupFloating): IView | null {

		const grid = new SrcGrid().initialize({
			reg: rib.reg,
			hideHeaders: true,
			autoSelOnFocus: "first",
			itemHandlingReact: rib.reg.env.infoBroker,
			defaultAction: FocusItemSelRef.SINGLETON,
			actions: rib.reg.mergeLists("actions:ribbonRef:shortDesc", "actions:wsp:shortDesc"),
			accelKeyMgr: new AccelKeyMgr().initFromMapActions(rib.reg.mergeListsAsMap("accelkeys:ribbonRef:shortDesc", "accelkeys:wsp:shortDesc")),
			emptyBody: () => <span>{rib._currentDetailsConf?.emptyGridMsg || 'Aucun item'}</span>
		});

		let confTitle = <div class="title"/> as HTMLDivElement;

		popup.addEventListener('c-show', async (ev: CustomEvent) => {
			//grid.dataHolder.setDatas([]);
			const wsp = rib.reg.env.wsp;
			confTitle.innerText = "";
			//grid.srcGridDatas.setDatas([]); flash UI

			if (rib.confs.length === 1) {
				rib._currentDetailsConf = rib.confs[0];
				confTitle.appendChild(<div>{rib._currentDetailsConf.label}</div>);
			} else {
				const select = confTitle.appendChild(<select onchange={async () => {
					//grid.srcGridDatas.setDatas([]); flash UI
					const pos = Number.parseInt(select.value);
					rib._currentDetailsConf = rib.confs[pos];
					const items = await rib._currentDetailsConf.fetchSrcList.call(rib, rib);
					grid.srcGridDatas.setDatas(items);
					grid.focus();
					if (pos === 0) rib.redrawShortView(items);
				}}/>) as HTMLSelectElement;
				rib.confs.forEach((entry, pos) => {
					select.appendChild(<option value={pos} label={entry.label} title={entry.description}/>);
				});
			}
			rib._currentDetailsConf = rib.confs[0];
			const items = await rib._currentDetailsConf.fetchSrcList.call(rib, rib);
			grid.srcGridDatas.setDatas(items);
			rib.redrawShortView(items);
			grid.focus();
		});

		return <div>
			<ShadowJsx skin="wsp-rib-refs" reg={rib.reg}>
				{confTitle}
				{grid}
			</ShadowJsx>
		</div>;
	}

	protected _refresh() {
		super._refresh();
		if (!this.hidden) {
			if (!this._noFetch)
				this.fetch(this).then((resp) => this.redrawShortView(resp));
			else
				this.redrawShortView();
		}

	}
}

