/**
 * Life-cyle de l'item.
 */
export class RibbonLc extends RibbonBase {

	template: (rib: RibbonLc) => TemplateResult;

	protected _initialize(init: any): void {
		super._initialize(init);
		this.reg.env.longDescChange.add(this.refresh.bind(this));
		this.template = this.reg.getSvc("ribbon.item.lc.tpl") || RibbonLc._template;
		this.setTooltip(RibbonLc._tooltip, true);
		//this.callClickOnMouseover();
		//this.onclick = (ev: MouseEvent) => {
		// 	POPUP.showPopupActions({
		// 		reg: this.reg,
		// 		actions: LIFECYCLE.listAllTransitionActions(this.reg.env.wsp.wspMetaUi),
		// 		actionContext: this.shortDescCtx,
		//		groupOrder: this.reg.env.wsp.wspMetaUi.getLcTransitionsGroupOrder(),
		// 		headBuilder: (root: HTMLElement) => {
		// 			root.append("ChangÃ© par ", <c-user>xx</c-user>, " le ", SRCDRAWER.dateFormatter.format(this.reg.env.longDesc.lcDt),
		// 				<div>Changer d'Ã©tat :</div>)
		// 		}
		// 	}, this, this);
		// }
	}

	static _template(this: void, rib: RibbonLc): TemplateResult {
		const longDesc = rib.reg.env.longDesc;
		const lcState = rib.reg.env.wsp.wspMetaUi.getLcStateOrUnknown(longDesc.lcSt);
		return xhtml`<div class="smallTi">${rib.reg.env.wsp.wspMetaUi.getLcName()}</div><div id="lcSt"><img src="${lcState.iconUrl || ""}"/>${lcState.name}</div>`;
	}

	static _tooltip(this: void, rib: RibbonLc, tooltip: PopupTooltip): IView | null {
		const tpl = <div id="ttRoot">
			<ShadowJsx skin={rib.localName} reg={rib.reg}>
				<div id="last">
					<div>ChangÃ© le <span id="lastDt"/></div>
					<div>par <c-user id="lastUser"/></div>
				</div>
				<BarActions class="vertical" Ã®={{
					reg: rib.reg,
					actions: LIFECYCLE.listAllTransitionActions(rib.reg.env.wsp.wspMetaUi),
					actionContext: rib.shortDescCtx,
					uiContext: "dialog",
					groupOrder: rib.reg.env.wsp.wspMetaUi.getLcTransitionsGroupOrder(),
					disableFullOverlay: true
				} as OBarActionsInit<IShortDescCtx>}/>
			</ShadowJsx>
		</div>;
		tooltip.addEventListener('c-show', () => {
			const ld = rib.reg.env.longDesc;
			if (ld.lcDt > 0) {
				DOM.setHidden(tpl.shadowRoot.getElementById("last"), false);
				DOM.setTextContent(tpl.shadowRoot.getElementById("lastDt"), LOCALE.formatDateDigitsToSec.format(ld.lcDt));
				DOM.setTextContent(tpl.shadowRoot.getElementById("lastUser"), ld.lcBy);
			} else {
				DOM.setHidden(tpl.shadowRoot.getElementById("last"), true);
			}
		});
		return tpl;
	}

	protected _refresh() {
		super._refresh();
		const longDesc = this.reg.env.longDesc;
		if (ITEM.isHistoryUri(longDesc.srcUri) || !LIFECYCLE.hasLcOnItem(longDesc, this.reg.env.wsp.wspMetaUi)) {
			DOM.setHidden(this, true);
		} else {
			DOM.setHidden(this, false);
			renderAppend(this.template(this), this.shadowRoot);
		}
	}
}

