/**
 * dynGen de l'item.
 */

export interface ORibbonDynGenInit extends OSkinableInit {
	dynGen: ODynGenInit,
	label?: string;
}

export class RibbonDynGen extends RibbonBase {

	template: (rib: RibbonDynGen) => TemplateResult;

	protected conf: ORibbonDynGenInit

	protected _initialize(init: ORibbonDynGenInit): void {
		super._initialize(init);
		this.conf = init;
		if (!this.conf.dynGen) this.conf.dynGen = {} as any;
		if (!("reg" in this.conf.dynGen)) this.conf.dynGen.reg = this.reg;
		if (!("autoResize" in this.conf.dynGen)) this.conf.dynGen.autoResize = true;
		if (!("w" in this.conf.dynGen)) this.conf.dynGen.w = this.reg.env.wsp.code;
		if (!("r" in this.conf.dynGen)) this.conf.dynGen.r = SRC.srcRef(this.reg.env.longDesc);

		this.reg.env.longDescChange.add(this.refresh.bind(this));
		this.template = this.reg.getSvc("ribbon.item.dynGen.tpl") || RibbonDynGen._template;
	}

	static _template(this: void, rib: RibbonDynGen): TemplateResult {
		const longDesc = rib.reg.env.longDesc;
		const dynGen = new DynGen().initialize(rib.conf.dynGen);
		dynGen.onViewShown();
		return xhtml`${dynGen}<div id="label">${rib.conf.label}</div>`;
	}

	protected _refresh() {
		super._refresh();
		const longDesc = this.reg.env.longDesc;
		if (ITEM.isHistoryUri(longDesc.srcUri)) {
			DOM.setHidden(this, true);
		} else {
			DOM.setHidden(this, false);
			renderAppend(this.template(this), this.shadowRoot);
		}
	}
}

