/**
 * Responsabilities de l'item.
 */
export class RibbonResp extends RibbonBase {

	template: (rib: RibbonResp) => TemplateResult;

	protected _initialize(init: any): void {
		super._initialize(init);
		this.reg.env.longDescChange.add(this.refresh.bind(this));
		this.template = this.reg.getSvc("ribbon.item.resp.tpl") || RibbonResp._template;
		this.setFloating(this.reg.getSvc("ribbon.item.resp.details") || RibbonResp._details, true);
	}

	static _template(this: void, rib: RibbonResp): TemplateResult {
		const longDesc = rib.reg.env.longDesc;
		const rspUsrs = RESPS.toRespsList(longDesc.rspUsrs);
		const itemType = rib.reg.env.wsp.wspMetaUi.getItemType(longDesc.itModel);
		let resps = <div class="list"/>;

		let count = 0;
		let hasMore = false;
		for (const resp of itemType.datas.resps) {//.sort((e1, e2) => e2.required === e1.required ? 0 : (e2.required ? 1 : -1))
			let nbUsers = rspUsrs[resp.code]?.length;
			if (rspUsrs[resp.code] && nbUsers > 0) {
				/*if (count >= 2) {
					hasMore = true;
					break;
				}*/
				const keyElt = resps.appendChild(<div class="key">{resp.name}</div>);
				if (resp.desc) keyElt.title = resp.desc;
				const values = resps.appendChild(<div class="values"/>);
				values.appendChild(<c-userref class="inline" Ã®={{reg: rib.reg, nickOrAccount: rspUsrs[resp.code][0]} as OUserRefInit}/>);
				if (--nbUsers > 0)
					values.appendChild(<span class="moreUsers">+{nbUsers}</span>)
				/*rspUsrs[resp.code].forEach((user) => {
					values.appendChild(<c-userref class="inline" Ã®={{reg: rib.reg, nickOrAccount: user} as OUserRefInit}/>);
				});*/
				count++;
			}
		}

		if (count == 0 && itemType.hasResps())
			resps.appendChild(<MsgLabel id="noRespLabel" Ã®={{label: "ResponsabilitÃ©s non dÃ©finies", level: rib.isInError() ? "error" : "info"} as OMsgLabelInit}/>);

		return xhtml`${resps}`;//${hasMore ? <MsgLabel class="more" Ã®={{label: "..."} as OMsgLabelInit}/> : ""}
	}

	static _details(this: void, rib: RibbonResp, popup: PopupFloating): IView | null {
		const longDesc = rib.reg.env.longDesc;
		const rspUsrs = RESPS.toRespsList(longDesc.rspUsrs);
		const itemType = rib.reg.env.wsp.wspMetaUi.getItemType(longDesc.itModel);
		let resps = <div class="list"/>;
		itemType.datas.resps.forEach((resp) => {
			// FIXME : afficher ici les resps inconnues de l'item ? A rÃ©Ã©valuer...
			const keyElt = resps.appendChild(<div class="key">{resp.name}</div>);
			if (resp.desc) keyElt.title = resp.desc;
			const inptELt = resps.appendChild(<RespEditField class="values" Ã®={{
				reg: rib.reg,
				resp: resp.code,
			} as ORespEditFieldInit}/>);
		});
		return <div>
			<ShadowJsx skin="wsp-rib-resp/details" reg={rib.reg}>
				<div class="title">ResponsabilitÃ©s</div>
				{resps}
			</ShadowJsx>
		</div>;
	}

	protected isInError(): boolean {
		const longDesc = this.reg.env.longDesc;
		return longDesc.rspSt === EItResp.errors
	}

	protected _refresh() {
		super._refresh();
		const longDesc = this.reg.env.longDesc;
		const itemType = this.reg.env.wsp.wspMetaUi.getItemType(longDesc.itModel);
		if (ITEM.isHistoryOrTrashUri(longDesc.srcUri) || !itemType.hasResps()) {
			DOM.setHidden(this, true);
		} else {
			DOM.setHidden(this, false);
			DOM.setAttrBool(this, "data-error", this.isInError());
			renderAppend(this.template(this), this.shadowRoot);
		}
	}
}

