/**
 * TÃ¢ches associÃ©es Ã  l'item.
 */
export class RibbonTasks extends RibbonBase {

	template: (rib: RibbonTasks) => TemplateResult;

	fetch: (rib: RibbonTasks) => Promise<void>;

	pending: number;
	forthcoming: number;

	private _noFetch = true;

	protected _initialize(init: any): void {
		super._initialize(init);
		if (!this.reg.env.wsp.wspMetaUi.hasTasks) {
			this.hidden = true;
			return;
		}
		this.fetch = this.reg.getSvc("ribbon.item.tasks.fetch") || this._fetch;
		this.template = this.reg.getSvc("ribbon.item.tasks.tpl") || RibbonTasks._template;
		this.setFloating(this.reg.getSvc("ribbon.item.tasks.details") || RibbonTasks._details);

		//on retarde l'affichage des infos du ribbon pour laisser la prioritÃ© au contenu de l'item.
		setTimeout(async () => {
			this._noFetch = false;
			this.refresh();
		}, 300);
	}

	async _fetch(this: void, rib: RibbonTasks) {
		const counts = await WSP.fetchSrc(rib.reg.env.wsp, rib, SRC.srcRef(rib.reg.env.longDesc), ["tkPendingCount", "tkForthcomingCount"]);
		rib.pending = counts.tkPendingCount || 0;
		rib.forthcoming = counts.tkForthcomingCount || 0;
	}

	static _template(this: void, rib: RibbonTasks): TemplateResult {
		const pending = rib.pending;
		const forthcoming = rib.forthcoming;
		return xhtml`<div><span id="pending" title="TÃ¢ches en cours">${pending !== undefined ? pending : '...'}</span>
<span id="forthcoming" title="TÃ¢ches Ã  venir">${forthcoming !== undefined ? `(${forthcoming})` : '...'}</span></div>
<div id="label">tÃ¢ches</div>`;
	}

	static _details(this: void, rib: RibbonTasks, popup: PopupFloating): IView | null {
		const tasks = new Tasks().initialize({
			reg: rib.reg,
			ephemeralView: true,
			refreshCb: (visibleStage: ETaskStage, tasks: JSrcFields[]) => {
				if (visibleStage === ETaskStage.pending) DOM.setTextContent(rib.shadowRoot.getElementById('pending'), tasks.length.toString());
				else if (visibleStage === ETaskStage.forthcoming) DOM.setTextContent(rib.shadowRoot.getElementById('forthcoming'), `(${tasks.length.toString()})`);
			}
		});

		return <ViewsContainer Ã®={{views: [tasks]} as OViewsContainerInit}>
			<ShadowJsx skin="wsp-rib-tasks/details" reg={rib.reg}>
				<div class="title">TÃ¢ches liÃ©es Ã  cet item</div>
				{tasks}
			</ShadowJsx>
		</ViewsContainer>;
	}

	protected _refresh() {
		super._refresh();
		if (!this.hidden) {
			if (!this._noFetch)
				this.fetch(this).then(() => renderAppend(this.template(this), this.shadowRoot));
			else
				renderAppend(this.template(this), this.shadowRoot)
		}
	}
}

